import React from 'react';
import { Message } from 'semantic-ui-react';

type PropsSuccess = {
  isValid: boolean;
  isFetching: boolean;
};

export const MessageWarning = () => (
  <Message
    warning
    header="Warning!"
    content="All form fields must be filled out."
  />
);

export const MessageSuccess: React.FC<PropsSuccess> = ({ isValid, isFetching }) => (
  <Message
    success={isValid && isFetching}
    header="Success!"
    content="All form fields are filled in correctly."
  />
);
